.choice-test {
  font-family: SourceHanSansCN-Bold;
  background: #fafafa;
  position: absolute;
  bottom: 0px;
  top: 0px;
  left: 0px;
  right: 0px;
}
.choice-test .home-bottom-line {
  padding-bottom: 60px;
  width: 17.5rem;
  margin: 0 auto;
  display: flex;
}
.choice-test .home-bottom-line span {
  text-align: center;
  font-family: SourceHanSansCN-Regular;
  font-size: 0.75rem;
  color: #cccccc;
  display: block;
  width: 7.5rem;
  overflow: hidden;
}
.choice-test .home-bottom-line div {
  overflow: hidden;
  border-bottom: 1px solid #cccccc;
  height: 0px;
  width: 5rem;
  position: relative;
  top: 0.375rem;
}
.choice-test .am-list-brief {
  text-align: right;
  font-size: 12px !important;
  color: rgba(40, 40, 40, 0.8);
}
.choice-test .bar-user {
  position: fixed;
  bottom: 10px;
  width: 100%;
  height: 50px;
  padding: 0 20px;
  box-sizing: border-box;
  box-shadow: 0px 2px 6px 0px rgba(40, 40, 40, 0.2);
}
.choice-test .my-list {
  background: #fafafa;
}
.choice-test .my-list .am-list-header {
  box-sizing: border-box;
  height: 2.25rem;
  border: none;
  background: -webkit-gradient(linear, left top, left bottom, from(#c9174b), color-stop(#e61550), to(#c9174b));
  background: linear-gradient(#c9174b, #e61550, #c9174b);
  border-bottom: none;
  text-align: center;
  line-height: 2.25rem;
  color: #f5f5f5;
  font-weight: bold;
  font-size: 1.125rem;
  padding: 0px;
  font-family: SourceHanSansCN-Bold;
}
.choice-test .my-list .am-list-body {
  border: none;
  width: 100%;
}
.choice-test .my-list .am-list-body .am-list-item {
  padding-left: 0px;
  width: 92%;
  height: 7.5rem;
  margin: 0 auto;
  overflow: hidden;
}
.choice-test .my-list .am-list-body .am-list-item .am-list-line {
  padding-right: 0px;
  padding-top: 0px;
  padding-bottom: 0px;
  box-sizing: border-box;
  overflow: hidden;
  height: 100%;
}
.choice-test .my-list .am-list-body .am-list-item .am-list-content {
  box-sizing: border-box;
  overflow: hidden;
  padding: 0 0;
  display: flex;
  height: 100%;
}
.choice-test .my-list .am-list-body .am-list-item .am-list-content .item-left {
  padding: 14px 0px;
  width: 5.625rem;
}
.choice-test .my-list .am-list-body .am-list-item .am-list-content .item-left img {
  width: 5.625rem;
  height: 5.625rem;
  border-radius: 0.375rem;
}
.choice-test .my-list .am-list-body .am-list-item .am-list-content .item-right {
  position: relative;
  padding-bottom: 14px;
  padding-top: 14px;
  margin-left: 12px;
  width: 100%;
}
.choice-test .my-list .am-list-body .am-list-item .am-list-content .item-right .hot-img {
  padding-left: 4px;
  margin-top: -6px;
  height: 1.25rem;
  width: 1.125rem;
}
.choice-test .my-list .am-list-body .am-list-item .am-list-content .item-right .test-count {
  position: absolute;
  top: 0.8125rem;
  right: 0px;
  font-size: 0.75rem;
  color: #c9174b;
}
.choice-test .my-list .am-list-body .am-list-item .am-list-content .item-right .test-button {
  position: absolute;
  bottom: 0.75rem;
  right: 0.25rem;
  width: 3.75rem;
  height: 1.25rem;
  font-size: 0.75rem;
  font-family: SourceHanSansCN-Medium;
  padding: 0 0.25rem;
  line-height: 1.25rem;
  color: #f5f5f5;
  background: -webkit-gradient(linear, left top, left bottom, from(#c9174b), color-stop(#e61550), to(#c9174b));
  background: linear-gradient(#c9174b, #e61550, #c9174b);
  border-radius: 0.3125rem;
  box-sizing: border-box;
  overflow: hidden;
  box-shadow: 0px 1.8px 4px 0px #e61550;
}
.choice-test .my-list .am-list-body .am-list-item .am-list-content .item-right .test-category {
  position: absolute;
  bottom: 0.875rem;
  display: block;
  border: 1px solid #2289cb;
  height: 0.75rem;
  text-align: center;
  line-height: 0.75rem;
  width: 1.875rem;
  font-size: 0.75rem;
  color: #2289cb;
  background: white;
  padding: 0.125rem 0.125rem;
  border-radius: 2px;
  font-family: SourceHanSansCN-Regular;
}
.choice-test .choice-result .user-list {
  box-sizing: border-box;
  height: 36px;
  border: none;
  background: -webkit-gradient(linear, left top, left bottom, from(#c9174b), color-stop(#e61550), to(#c9174b));
  background: linear-gradient(#c9174b, #e61550, #c9174b);
  border-bottom: none;
  padding: 0 14px;
  line-height: 36px;
  color: #f5f5f5;
  font-weight: bold;
  font-size: 16px;
  font-family: SourceHanSansCN-Bold;
}
.choice-test .choice-result .am-wingblank {
  overflow-x: scroll;
}
.choice-test .choice-result .user-win {
  background: white;
  overflow: hidden;
  padding: 10px 0px 0px 0px;
  height: 142px;
  background: #e61550;
}
.choice-test .choice-result .space-carousel {
  overflow: hidden;
  display: flex;
  position: relative;
}
.choice-test .choice-result .space-carousel .item-win {
  height: 100%;
  overflow: hidden;
  flex: 1 1;
  font-family: SourceHanSansCN-Regular;
}
.choice-test .choice-result .space-carousel .item-win div {
  margin: 0 auto;
  width: 100px;
  border-radius: 5px;
  height: 114px;
  background-repeat: no-repeat;
  background-size: cover;
}
.choice-test .choice-result .space-carousel .item-win span {
  margin: 0 auto;
  display: block;
  text-align: center;
  width: 100px;
  overflow: hidden;
  line-height: 30px;
  font-size: 12px;
  color: #fafafa;
}
.choice-test .choice-result .space-carousel .item-win span span {
  display: inline;
}
.login-user {
  position: fixed;
  bottom: 0px;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.5);
  overflow: hidden;
}
.login-user .login-text {
  width: 70%;
  height: 105px;
  border-radius: 10px;
  position: absolute;
  background: #f5f5f5;
  top: 40%;
  overflow: hidden;
  left: 15%;
  padding: 0px 14px 14px 14px;
  box-sizing: border-box;
}
.login-user .login-text .input-home-user {
  font-size: 16px;
  color: #333333;
  text-align: center;
  overflow: hidden;
  margin-top: 22px;
  margin-bottom: 14px;
}
.login-user .login-text .cross {
  position: absolute;
  right: 4px;
  color: #cccccc;
}
.login-user .login-text .input-nickname {
  width: 115px;
  height: 30px !important;
  border-radius: 8px;
  background: #e6e6e6;
  color: #f5f5f5;
  line-height: 30px;
  min-height: 30px;
}
.login-user .login-text .test-button {
  position: absolute;
  bottom: 20px;
  right: 14px;
  width: 86px;
  height: 30px;
  font-size: 16px;
  font-family: SourceHanSansCN-Medium;
  padding: 0px 4px;
  line-height: 30px;
  text-align: center;
  color: #f5f5f5;
  background: -webkit-gradient(linear, left top, left bottom, from(#c9174b), color-stop(#e61550), to(#c9174b));
  background: linear-gradient(#c9174b, #e61550, #c9174b);
  border-radius: 5px;
  box-sizing: border-box;
  overflow: hidden;
  box-shadow: 0px 1.8px 4px 0px #e61550;
}
.login-user .input-nickname {
  height: 60px !important;
}
.login-user .am-input-label {
  color: #141414 !important;
}
.login-user .am-button {
  width: 90%;
  margin: 40px auto;
  box-sizing: border-box;
  background: #33a0ff;
}
.text-detail {
  font-family: SourceHanSansCN-Bold;
  background: #fafafa;
}
.text-detail .am-navbar-title {
  font-size: 1.125rem;
}
.text-detail .am-icon-md {
  width: 1.375rem;
  height: 1.375rem;
}
.text-detail .am-navbar {
  background: #fafafa;
  position: fixed;
  box-shadow: 0px 2px 6px 0px rgba(40, 40, 40, 0.1);
  top: 0px;
  width: 100%;
  overflow: hidden;
  z-index: 1000;
  height: 2.8125rem;
}
.text-detail .am-navbar .am-navbar-title {
  color: #141414;
}
.text-detail .am-navbar .am-icon {
  color: #cccccc;
  font-size: 1rem;
}
.text-detail .test-start {
  margin-top: 3.75rem;
  padding-top: 1.875rem;
  background: #fafafa;
  position: absolute;
  bottom: 0px;
  top: 0px;
  left: 0px;
  right: 0px;
}
.text-detail .test-start > img {
  display: block;
  width: 8.75rem;
  margin: 0 auto;
  border-radius: 4.375rem;
}
.text-detail .test-start .title {
  text-align: center;
  margin: 1.25rem 0px;
}
.text-detail .test-start .title .title-1 {
  line-height: 1.875rem;
  color: black;
  font-size: 1.125rem;
  font-weight: bold;
  font-family: SourceHanSansCN-Bold;
}
.text-detail .test-start .title .title-2 {
  font-size: 0.875rem;
  color: rgba(0, 0, 0, 0.8);
}
.text-detail .test-start .second {
  font-size: 0.875rem;
  line-height: 1.75rem;
  width: 90%;
  margin: 1.25rem auto;
  color: rgba(0, 0, 0, 0.8);
}
.text-detail .test-start .second .third-title {
  font-size: 0.875rem;
  font-weight: bold;
  color: #c9174b;
}
.text-detail .test-start .test-start-btn {
  width: 11.625rem;
  margin: 0 auto;
  height: 2rem;
  font-size: 1rem;
  font-family: SourceHanSansCN-Medium;
  padding: 0 0.25rem;
  line-height: 1.875rem;
  text-align: center;
  color: #f5f5f5;
  background: -webkit-gradient(linear, left top, left bottom, from(#c9174b), color-stop(#e61550), to(#c9174b));
  background: linear-gradient(#c9174b, #e61550, #c9174b);
  border-radius: 0.5rem;
  box-sizing: border-box;
  box-shadow: 0px 1.8px 4px 0px #e61550;
  border: none;
  overflow: hidden;
}
.test-result-wap {
  overflow: auto;
  height: 100vh;
  overflow-x: hidden;
  font-family: MicrosoftYaHei-Bold;
}
.test-result-wap .handle-bar {
  position: fixed;
  bottom: 1.25rem;
  margin: 1.25rem 0px 2px 0px;
  font-size: 1rem;
  width: 100%;
  display: flex;
  z-index: 1000;
  justify-items: center;
}
.test-result-wap .handle-bar div {
  margin: 0 auto;
  width: 46vw;
  height: 2rem;
  font-size: 1rem;
  font-family: SourceHanSansCN-Medium;
  line-height: 2rem;
  text-align: center;
  color: #f5f5f5;
  background: -webkit-gradient(linear, left top, left bottom, from(#2289cb), color-stop(#2289cb), to(#2289cb));
  background: linear-gradient(#2289cb, #2289cb, #2289cb);
  border-radius: 0.5rem;
  box-sizing: border-box;
  border: none;
  overflow: hidden;
}
@font-face {
  font-family: ZhongHei;
  src: url(/web/static/media/zhonghei.896ca9e2.ttf);
}
.scroll-test .am-steps-item-icon {
  background: #33a0ff;
  border: none;
}
.scroll-test .am-steps-icon {
  font-family: SourceHanSansCN-Bold;
  font-weight: bold;
}
.scroll-test ul {
  padding: 0 0;
  margin: 0 0;
}
.scroll-test .scroll-progress > div {
  border: 1.5px solid #e61550;
  box-shadow: 2px 0px 1px 0px #e61550;
}
.bar-react {
  width: 100vw;
}
.bar-react > div {
  height: 18.75rem;
  margin: 1.25rem 0;
  background-color: white;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.08);
  font-family: SourceHanSansCN-Regular;
}
.share-handle {
  width: 100%;
  height: 12.5rem;
  background: white;
  font-family: SourceHanSansCN-Bold;
  padding-top: 2.5rem;
  overflow: hidden;
}
.share-handle ul {
  width: 80%;
  margin: 0 auto;
  height: 6.25rem;
  display: flex;
  justify-content: space-around;
  justify-items: center;
}
.share-handle ul li {
  width: 3.75rem;
}
.share-handle ul li img {
  width: 100%;
}
.share-handle ul li span {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 0.75rem;
  color: #444;
}
.right-li > div {
  float: right;
  margin-right: 0.625rem;
}
.right-li .dot-right {
  background: white;
  position: absolute;
  width: 0.5rem;
  right: -0.25rem;
  top: 0.5rem;
  height: 0.5rem;
  -webkit-transform: skewX(-40deg);
          transform: skewX(-40deg);
}
.dot-left {
  background: white;
  position: absolute;
  width: 0.5rem;
  left: -0.25rem;
  top: 0.5rem;
  height: 0.5rem;
  -webkit-transform: skewX(40deg);
          transform: skewX(40deg);
}
.myimg {
  width: 100%;
}
